import React, { useEffect, useState } from 'react';
import { fetchRequests, deleteDemand } from '../services/api';
import { TableSortLabel, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button } from '@mui/material';
import { IconButton, Tooltip } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

export default function RequestList({ onSelect }) {
  const [requests, setRequests] = useState([]);
const [order, setOrder] = useState('asc');
 const [suppressionDetect,setSupp] = useState(null);
  const [orderBy, setOrderBy] = useState('statut');

  useEffect(() => {
    fetchRequests().then(res => setRequests(res.data));
  }, []);

 const handleSort = (property) => {

    const isAsc = orderBy === property && order === 'asc';

    setOrder(isAsc ? 'desc' : 'asc');

    setOrderBy(property);

  };
  const sortedRequests = [...requests].sort((a, b) => {

    const aValue = a.demande[orderBy]?.toLowerCase() || '';

    const bValue = b.demande[orderBy]?.toLowerCase() || '';

    return (order === 'asc' ? aValue.localeCompare(bValue) : bValue.localeCompare(aValue));

  });
  
  async function deleteDemande(immatriculation,id){
	let formData = new FormData();
    formData.append("idDemande", id);
    formData.append("immatriculation", immatriculation);
    try {
        const res = await deleteDemand(immatriculation,id);

		console.log(res);
        return res;
    } catch (err) {
        console.error('Erreur lors de l’envoi :', err);
        alert('Une erreur est survenue.');
        return null;
    }
	  
  }
  
      
  return (

<TableContainer component={Paper}>
  <Table>
    <TableHead>
      <TableRow>
        <TableCell><b>Nom</b></TableCell>
        <TableCell><b>Immatriculation</b></TableCell>
        <TableCell><TableSortLabel active={orderBy === 'Statut'} direction={orderBy === 'statut' ? order : 'asc'} onClick={()=> handleSort('statut')}><b>Statut</b></TableSortLabel></TableCell>
        <TableCell><b>Type démarche</b></TableCell>
        <TableCell><b>Type demandeur</b></TableCell>
        <TableCell><b>Action</b></TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      {sortedRequests.map((req) => (
        <TableRow key={req.id}>
          <TableCell>{req.utilisateur.nom}</TableCell>
          <TableCell>{req.demande.immatriculation}</TableCell>
          <TableCell>{req.demande.statut}</TableCell>
          <TableCell>{req.demande.typeDemande}</TableCell>
          <TableCell>{req.demande.type}</TableCell>
          <TableCell>
            <Button onClick={() => onSelect(req.utilisateur.cgplusUsers)}>Détails</Button>
          </TableCell>
		  <TableCell>
			<IconButton onClick={()=>{
				deleteDemande(req.demande.immatriculation,req.demande.cgplusDemandes)
				
			}}color="error">
				<DeleteIcon />
			</IconButton>
		  </TableCell>
        </TableRow>
      ))}
    </TableBody>
  </Table>
</TableContainer>

    
  );
}

