import React, { useState } from 'react';
import RequestList from './components/RequestList';
import RequestDetails from './components/RequestDetails';
import {useAuth0} from '@auth0/auth0-react';

function Admin() {
  const [selectedRequestId, setSelectedRequestId] = useState(null);
const { logout } = useAuth0();
  return (
  <div>
  <button onClick={() => logout({ returnTo: 'http://localhost:3000/login' })}>
        Se déconnecter
      </button>
 <div style={{ padding: '20px' }}>
      <h1>Interface Agent – Carte Grise</h1>
      {/* Si aucune demande n'est sélectionnée, on affiche la liste */}
      {!selectedRequestId && (
        <RequestList onSelect={setSelectedRequestId} />
      )}

      {/* Si une demande est sélectionnée, on affiche les détails */}
      {selectedRequestId && (
        
        <RequestDetails selectedRequestId={selectedRequestId} onBack={() => setSelectedRequestId(null)} />

      )}
    </div>
	
</div>
  );
}

export default Admin;
