import axios from 'axios';

//const API_BASE = 'https://www.cgplusautos.fr'; // à adapter
const API_BASE = 'http://localhost:3001'; // à adapter


export const fetchRequests = () => axios.get(`${API_BASE}/agent/demandes/new`);
export const fetchRequestDetails = (id) => axios.get(`${API_BASE}/agent/demandes/${id}`);
export const fetchSignedURL = (key) => axios.get(`${API_BASE}/agent/demandes/signedURL/${encodeURIComponent(key)}`);
export const addPrice = (idDemande,price) => axios.get(`${API_BASE}/agent/demandes/addPrice/${encodeURIComponent(price)}/id/${encodeURIComponent(idDemande)}`);
export const sendPayment = (formData) => axios.post(`${API_BASE}/agent/demandes/send-payment-email`,formData);
export const toStateInProgressSIV = (state,idDemande) => axios.post(`${API_BASE}/agent/demandes/${idDemande}/changeState/${state}`);
export const toStateInProgressClose = (state,idDemande) => axios.post(`${API_BASE}/agent/demandes/${idDemande}/changeState/${state}`);
export const deleteDemand = (immatriculatioin,idDemande) => axios.post(`${API_BASE}/agent/demande/delete/${idDemande}/${immatriculatioin}`);

//export const updateRequest = (id, data) => axios.put(`${API_BASE}/requests/${id}`, data);
