
import React, { useEffect, useState,useRef } from 'react';
import { toStateInProgressClose,toStateInProgressSIV,addPrice, sendPayment,fetchRequestDetails, fetchSignedURL } from './../services/api';
import { STATE_NEW, STATE_WAIT_PAYMENT,STATE_IN_PROGRESS, STATE_IN_PROGRESS_SIV, STATE_CLOSE } from './../../Constantes';

export default function RequestDetails({ selectedRequestId, onBack }) {
  const [request, setRequest] = useState(null);
  const [enrichedFiles, setEnrichedFiles] = useState([]);
  const [price, setPrice] = useState('');
  const [state, setState] = useState('');
	const priceInputRef= useRef();
  // Récupération des détails de la demande
  useEffect(() => {
    if (!selectedRequestId) return;

    fetchRequestDetails(selectedRequestId).then(res => {
      console.log("Détails de la demande :", res.data);
	  console.log("Détails de la demande :", res.data.demande);

	  setPrice(res.data.demande.prix);
	  setState(res.data.demande.statut);
      setRequest(res.data);
    });
  }, [selectedRequestId]);

  // Récupération des URLs signées
  useEffect(() => {
    if (!request?.piecesJointes || request.piecesJointes.length === 0) return;

    //Récupération des URL signé pour permettre la consultation des documents dans le S3 privé
    const enrichFilesWithUrls = async () => {
      try {
        const enriched = await Promise.all(
          request.piecesJointes.map(async (file) => {
            const res = await fetchSignedURL(file.id);
            console.log("URL signée reçue :", res.data);
            return { ...file, url: res.data.url };
          })
        );
        setEnrichedFiles(enriched);
      } catch (error) {
        console.error("Erreur lors de la récupération des URLs signées :", error);
      }
    };

    enrichFilesWithUrls();
  }, [request?.piecesJointes]);
  const beforeAddPrice = async () =>{
	  const value = priceInputRef.current?.value;
	  if(value){
		  	setPrice(value); 
			addPrice(request.demande.cgplusDemandes,value);
	  }
	  
  };
  const handleSendPaymentEmailAndSaveBDD = async () => {
	const formData = new FormData(); 
	formData.append("requestId",request.demande.cgplusDemandes); 
	formData.append("montant",price); 
	formData.append("immatriculation",request.demande.immatriculation); 
	formData.append("mailClient",request.utilisateur.mail); 

	
    try {
     sendPayment(formData);
      alert("Email envoyé avec succès !");
    } catch (error) {
      console.error("Erreur lors de l'envoi de l'email :", error);
      alert("Échec de l'envoi de l'email.");
    }
  };
  const goStateInProgressSIV = async () => {
    try {
		toStateInProgressSIV(STATE_IN_PROGRESS_SIV,request.demande.cgplusDemandes)
		alert("Passe l'état de la demande à en cours dans le SIV");
    } catch (error) {
		console.error("Erreur lors de l'envoi de l'email :", error);
		alert("Echec du passage de la demande à en cours dans le SIV");
    }
  };
  const goToStateClose = async () => {
    try {
		toStateInProgressClose(STATE_CLOSE,request.demande.cgplusDemandes)
		alert("Demande fermée");
    } catch (error) {
		console.error("Erreur lors de l'envoi de l'email :", error);
		alert("Échec de la fermeture de la demande");
    }
  };

  if (!request) return <p>Chargement...</p>;

  return (
    <div>
      <button onClick={onBack}>← Retour</button>
      <h2>Détails de la demande</h2>
      <p><strong>Utilisateur :</strong> {request.utilisateur.nom}</p>
      <p><strong>Adress mail :</strong> {request.utilisateur.mail}</p>

      <p><strong>Pièces jointes :</strong></p>
      <ul>
        {enrichedFiles.length === 0 ? (
          <li>Chargement des fichiers...</li>
        ) : (
          enrichedFiles.map((file, idx) => (
            <li key={idx}>
              {file.url ? (
                <a href={file.url} target="_blank" rel="noopener noreferrer">
                  {file.name}
                </a>
              ) : (
                <span>{file.name} (URL non disponible)</span>
              )}
            </li>
          ))
        )}
      </ul>
		{price !== 'N/A' ? (
      <p><strong>Montant à payer :</strong> {price} €</p>
    ) : (
      <>
        <label>Montant à payer :</label><br />
        <input
          type="number"
		  ref={priceInputRef}
        /><br />
        <button onClick={beforeAddPrice}>Valider le tarif</button>

      </>
    )}
	{
	((state == STATE_NEW) || (state === STATE_WAIT_PAYMENT)) ? (
	<button onClick={handleSendPaymentEmailAndSaveBDD}>Valider la demande</button>
	): (<></>)
	
	}
	{
	state == STATE_IN_PROGRESS ? (
	<button onClick={goStateInProgressSIV}>Valider la création du dossier sur ANTS</button>
	): (<></>)
	
	}
	{
	state == STATE_IN_PROGRESS_SIV ? (
	<button onClick={goToStateClose}>Clore la demande</button>
	): (<></>)
	}
    </div>
  );
}
